.theme-dark {
    --bs-primary: #375a7f;
    --bs-secondary: #444;
    --bs-white: #fff;    
    --bs-grey: #888;     
    --bs-body-bg: #222; 
    --bs-anchor-color: #158cba;
    --bs-anchor-hover: #0d5875;
    --bs-border-color1:#222;
    --bs-border-color2: #739ac2;
    --bs-border-color4: #28415b;  
    --bs-border-color6: #2b2a2a;    
    --bs-border-color10: #444;                  
    --bs-text-color1: #fff; 
    --bs-text-color2: #fff;  
    --bs-text-color3: #262626; 
    --bs-text-color5: #999;    
    --bs-text-color6: #999;                 
    --bs-background-color1:#444; 
    --bs-background-color5: #E0E0E0;   
    --bs-background-color7: #303030;    
    --bs-background-color9: #303030;
    --bs-background-color12: #303030;             
    --bs-background-color16: transparent;       
    --bs-box-shadow-color1: rgba(55, 90, 127, 0.251);
    --bs-box-shadow-color2: rgba(55, 90, 127, 0.251);
    --bs-box-shadow-color5: rgba(255, 255, 255, 0.149);
}

.theme-light {
    --bs-primary: #3459e6;
    --bs-secondary: #fff;
    --bs-white: #fff;   
    --bs-grey: #888;         
    --bs-body-bg: #fff;
    --bs-anchor-color: #3459e6;
    --bs-anchor-hover: #2a47b8;
    --bs-border-color1:#ced4da;
    --bs-border-color2: #9aacf3;    
    --bs-border-color4: #2a47b8;    
    --bs-border-color6: #dfe0e1;         
    --bs-border-color10: #dee2e6;     
    --bs-text-color1: #495057;
    --bs-text-color2: #212529;
    --bs-text-color3: #fff;
    --bs-text-color5: #fff;       
    --bs-text-color6: #000;                             
    --bs-background-color1:#fff;       
    --bs-background-color5: #3459e6; 
    --bs-background-color7: #fff;    
    --bs-background-color9: #212529;      
    --bs-background-color12: #f8f9fa;        
    --bs-background-color16: #ccc;              
    --bs-box-shadow-color1: rgba(52, 89, 230, 0.251);
    --bs-box-shadow-color2: rgba(0, 0, 0, 0.051);
    --bs-box-shadow-color5: rgba(217, 217, 217, 0.502);
  }

/** bootstrap **/
body {
    color: var(--bs-text-color1);
    background-color: var(--bs-body-bg) !important;
}

a {
    color: var(--bs-anchor-color);
}

a:hover {
    color: var(--bs-anchor-hover);
}

a.text-primary:focus, a.text-primary:hover {
    color: var(--bs-primary) !important;
}

a.text-secondary:focus, a.text-secondary:hover {
    color: var(--bs-primary) !important;
}

h1, .h1 {
    font-size: 3rem;
}

h2, .h2 {
    font-size: 2.5rem;
}

h3, .h3 {
    font-size: 2rem;
}

h4, .h4 {
    font-size: 1.40625rem;
}

h5, .h5 {
    font-size: 1.171875rem;
}

h6, .h6 {
    font-size: 0.9375rem;
}

.form-control {
    font-size: 0.9375rem;
    color: var(--bs-text-color1);
    border-color: var(--bs-border-color1);
    background-color: var(--bs-background-color1);    
}

.form-control:-moz-focusring {
    text-shadow: 0 0 0 var(---bs-text-color2);
}

.form-control:focus {
    color: var(--bs-text-color2);
    border-color: var(--bs-border-color1);
    background-color: var(--bs-background-color1);    
    box-shadow: 0 0 0 0.2rem var(--bs-box-shadow-color1);    
}

.form-control::-webkit-input-placeholder {
    color: var(--bs-grey) !important;
}

.form-control::-ms-input-placeholder {
    color: var(--bs-grey) !important;
}

.form-control::placeholder {
    color: var(--bs-grey) !important;
}

.form-control:disabled, .form-control[readonly] {
    color: var(--bs-grey);
    /* background-color: var(--bs-background-color2); */
    background-color: var(--bs-border-color10) !important;
}

/* 
select.form-control:focus::-ms-value {
    color: var(--text-color2);
} 
*/

.btn {
    font-size: 0.9375rem;
    color: var(--bs-text-color2) !important;
}

.btn:hover {
    color: var(--bs-text-color2) !important;
}

.btn:focus, .btn.focus {
    box-shadow: 0 0 0 0.2rem var(--bs-box-shadow-color2);
}

.btn-primary {
    color: var(--bs-white) !important;
    background-color: var(--bs-primary);
    border-color: transparent;
}

.btn-primary:hover {
    color: var(--bs-white) !important;
    background-color: var(--bs-border-color4);
    border-color: transparent;
}

.btn-primary:focus, .btn-primary.focus {
    color: var(--bs-white) !important;
    background-color: var(--bs-border-color4);
    border-color: transparent;
    box-shadow: 0 0 0 0.2rem var(--bs-box-shadow-color1);
}

.btn-primary.disabled, .btn-primary:disabled {
    background-color: var(--bs-primary);
    border-color: var(--bs-primary);    
}

.btn-primary:not(:disabled):not(.disabled):active, .btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
    color: var(--bs-white) !important;
    background-color: var(--bs-border-color4);
    border-color: transparent;
}

.btn-primary:not(:disabled):not(.disabled):active:focus, .btn-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-primary.dropdown-toggle:focus {
    color: var(--bs-white) !important;
    box-shadow: 0 0 0 0.2rem var(--bs-box-shadow-color1);
}

.btn-secondary {
    color: var(--bs-text-color2) !important;
    background-color: var(--bs-secondary);
    border-color: var(--bs-background-color16);
}

.btn-secondary:hover {
    color: var(--bs-text-color2) !important;    
    background-color: var(--bs-border-color6);
    border-color: transparent;
    /* border-color: var(--bs-border-color6); */
}

.btn-secondary:focus, .btn-secondary.focus {
    color: var(--bs-text-color2) !important;    
    background-color: var(--bs-border-color6);
    border-color: transparent;
    /* border-color: var(--bs-border-color6); */
    box-shadow: 0 0 0 0.2rem var(--bs-box-shadow-color2);
}

.btn-secondary.disabled, .btn-secondary:disabled {
    background-color: var(--bs-secondary);
    /* border-color: var(--bs-border-color6); */
    border-color: var(--bs-background-color16);   
}

.btn-secondary:not(:disabled):not(.disabled):active, .btn-secondary:not(:disabled):not(.disabled).active,
.show > .btn-secondary.dropdown-toggle {
    color: var(--bs-text-color2) !important;
    background-color: var(--bs-border-color6);
    border-color: var(--bs-background-color16);
}

.btn-secondary:not(:disabled):not(.disabled):active:focus, .btn-secondary:not(:disabled):not(.disabled).active:focus,
.show > .btn-secondary.dropdown-toggle:focus {
    color: var(--bs-text-color2) !important;
    box-shadow: 0 0 0 0.2rem var(--bs-box-shadow-color2);
}

.btn-link {
    color: var(--bs-anchor-color);
}

.btn-link:hover {
    color: var(--bs-anchor-hover);
}

/* .btn-link:disabled, .btn-link.disabled {
    color: var(--bs-border-color3);
    opacity: 0.65;
} */

.btn-lg, .btn-group-lg > .btn {
    font-size: 1.171875rem;
}

.btn-sm, .btn-group-sm > .btn {
    font-size: 0.8203125rem;
}

.btn-group-vertical > .btn.active, .btn-group-vertical > .btn:active, .btn-group-vertical > .btn:focus, .btn-group > .btn.active, .btn-group > .btn:active, .btn-group > .btn:focus {
    z-index: 0;
}

.dropdown-menu {
    font-size: 0.9375rem;
    color: var(--bs-text-color1);
    background-color: var(--bs-background-color1);
    border-color: var(--bs-border-color6);    
}

.dropdown-divider {
    /* border-top: 1px solid var(---bs-background-color2); */
    border-top: 1px solid var(--bs-border-color10);
}

.dropdown-item {
    color: var(--bs-text-color2);
}

.dropdown-item:hover, .dropdown-item:focus {
    color: var(--bs-text-color2);
    background-color: var(--bs-box-shadow-color5);
}

.dropdown-item a {
    color: var(--bs-text-color2) !important;
}

.dropdown-item.active, .dropdown-item:active {
    color: var(--bs-text-color3) !important;
    background-color: var(--bs-background-color5) !important;
}

.dropdown-item.active, .dropdown-item:active a {
    color: var(--bs-text-color3) !important;
    background-color: var(--bs-background-color5) !important;
}

.dropdown-item.disabled, .dropdown-item:disabled {
    color: var(--bs-grey);
}

/* .dropdown-header {
    font-size: 0.8203125rem;
    color: var(--bs-border-color3);
} */

.dropdown-item-text {
    color: var(--bs-text-color2);
}

.dropdown-item.selected a {
    color: var(--bs-text-color3) !important;
    background-color: var(--bs-background-color5) !important;
}

.dropdown-item.selected.highlighted a {
    color: var(--bs-text-color3) !important;
    background-color: var(--bs-background-color5) !important;
}

.custom-control {
    min-height: 1.40625rem;
}

.custom-control-input {
    height: 1.203125rem;
}

.custom-control-input:checked ~ .custom-control-label::before {
    border-color: var(--bs-primary);
    background-color: var(--bs-primary);
}

.custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 0.2rem var(--box-shadow-color1);
}

.custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
    border-color: var(--border-color4);
}

.custom-control-input:not(:disabled):active ~ .custom-control-label::before {
    background-color: var(--background-color30);
    border-color: var(--background-color30);
}

.custom-control-input[disabled] ~ .custom-control-label, .custom-control-input:disabled ~ .custom-control-label {
    opacity: 0.5;
}

/* .custom-control-input[disabled] ~ .custom-control-label::before, .custom-control-input:disabled ~ .custom-control-label::before {
    opacity: 0.5;
} */

.custom-control-label::before {
    top: 0.203125rem;
}

.custom-control-label::after {
    top: 0.203125rem;
}

.custom-switch {
    padding-left: 2.5rem;
}

.custom-switch .custom-control-label::before {
    left: -2.25rem;
    width: 1.75rem;
    pointer-events: all;
    border-radius: 0.5rem;
}

.custom-switch .custom-control-label::after {
    top: calc(0.203125rem + 2px);
}

.custom-select {
    font-size: 0.9375rem;
    color: var(--bs-text-color1);
    background-color: var(--bs-background-color1) !important;
    /* background: var(--bs-background-color1) url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='4' height='5' viewBox='0 0 4 5'%3e%3cpath fill='%23303030' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e") no-repeat right 0.75rem center/8px 10px !important; */
    border-color: var(--bs-border-color1) !important;     
}

.custom-select:focus {
    border-color: var(--border-color4);
    box-shadow: 0 0 0 0.2rem var(--box-shadow-color1);
}

.custom-select:focus::-ms-value {
    color: var(--text-color2);
}

.custom-select:-moz-focusring {
    text-shadow: 0 0 0 var(--text-color2);
}

.nav-link {
    padding: 0.5rem 2rem;
}

.nav-link.disabled {
    opacity: 0.65;
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
    background-color: var(--bs-primary);
}

.navbar {
    padding: 1rem 1rem;
}

.navbar-brand {
    padding-top: 0.32421875rem;
    padding-bottom: 0.32421875rem;
    font-size: 1.171875rem;
}

.navbar-toggler {
    font-size: 1.171875rem;
}

.navbar-dark .navbar-nav .nav-link {
    color: var(--bs-text-color5);
}

.navbar-dark .navbar-nav .nav-link:hover, .navbar-dark .navbar-nav .nav-link:focus {
