}

.navbar-dark .navbar-toggler {
    color: var(--bs-text-color6);
}

.navbar-dark .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 0.6%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

.navbar-dark .navbar-text {
    color: var(---bs-text-color6);
}

@media (min-width: 992px) {
    .navbar-expand .navbar-collapse {
        display: block;
    }
}

.card {
    background-color: var(--bs-background-color12);
}

.card-header {
    background-color: var(--bs-background-color12);
    border-bottom: 2px solid var(--bs-border-color10);
}

.card-footer {
    background-color: var(--bs-background-color12) !important;
}

.badge-primary {
    background-color: var(--bs-primary);
}

a.badge-primary:hover, a.badge-primary:focus {
    background-color: var(--bs-border-color4);
}

a.badge-primary:focus, a.badge-primary.focus {
    box-shadow: 0 0 0 0.2rem var(--bs-box-shadow-color1);;
}

.badge-secondary {
