    fill: var(--bs-text-color1) !important;
}     

.fusioncharts-container>svg>g:first-of-type>g:nth-last-of-type(2)>g>g>text {
    fill: var(--bs-text-color1) !important;
}

.fusioncharts-container>svg>g:first-of-type>g:nth-last-of-type(8)>text:first-of-type {
    fill: var(--bs-text-color1) !important;
}

.fusioncharts-container>svg>g:first-of-type>g:nth-last-of-type(7)>text:first-of-type {
    fill: var(--bs-text-color1) !important;
}

.fusioncharts-container>svg>g:first-of-type>g:nth-last-of-type(8)>text:nth-of-type(2) {
    fill: var(--bs-grey) !important;
}

.fusioncharts-container>svg>g:first-of-type>g:nth-last-of-type(7)>text:nth-of-type(2) {
    fill: var(--bs-grey) !important;
}

.fusioncharts-container>svg>g>g:last-of-type>g>g>rect {
    fill: var(--bs-border-color10) !important;
    stroke: var(--bs-border-color1) !important;
}    

.fusioncharts-container > svg + div {
