    box-shadow: none !important;
    border-color: #222 !important;
}      
.fusioncharts-container > svg + div div {
    background:#444 !important;
}             

.fusioncharts-container > svg + div div span {
    color: #fff !important;        
    background:#444 !important;
}     

.fusioncharts-container > svg + div div span:hover {
    background-color: rgba(255,255,255,.15) !important;
}    

