body {
    font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 0.9375rem;    
    padding-bottom: 30px;
}

html {
    position: relative;
    min-height: 100%;
}

.footer {
    position: absolute;
    bottom: 0;
    width: auto;
    height: 30px;
    line-height: 30px;
}

.body-content {
    padding-top: 15px;
    padding-left: 15px;
    padding-right: 15px;
}

.img-round {
    position: relative;
    height: 0;
    padding-bottom: 100%;
    overflow: hidden;
    border-radius: 50%;
}

.img-round > img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

input:not(.btn):not([type=checkbox]):not([type=radio]):not(.ui-pg-input),
select:not(.ui-pg-selbox) {
    height: 32px;
}

input.date {
    font-size: 20px !important;
    padding: 0px 10px 0 40px !important;
    font-weight: 200;
    cursor: pointer;
    width: 170px !important;
}

.icon-wrapper {
    position: relative;
    font-size: 20px;
    display: block;
    line-height: 32px;
}

