a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

/** vue-select **/
.vue-select {
    width: auto;
    border: none;
}

.vue-dropdown-item {
    font-family: Arial,Helvetica,sans-serif;
    font-size: 15px;
    padding: 4px 24px 4px 24px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.vue-dropdown-item.group {
    font-weight: bold;
    padding-left:15px;
    pointer-events: none;
}

.vue-dropdown-item.highlighted:not(.group) {
    text-decoration: none;
    cursor: pointer;
    border: none !important;
}

.vue-select-header {
    padding: 3px;
}

.vue-input {
    padding: 0px;
}

.vue-tags {
    height: 32px;
}

.vue-input input {
    font-size: 0.9375rem;
    padding: 6px 12px 6px 12px;
    height: 32px;
}

.vue-dropdown {
    /* border: none; */
    max-height: none !important;
    overflow-y: auto;
}

/* .vue-tags[data-disabled="true"] {
    color: #888 !important;
} */

.vue-tag.selected {
    padding: 2px 5px;
    font-size: 12px;
    font-weight: 500;
    margin-right: 2px;
    margin-bottom: 1px;
}

.vue-tag.selected .icon.delete {
    content: "\f00d";
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    /* color: #fff; */
}

/** vue-final-modal **/
:deep(.modal-container) {
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-container {
    overflow-x: hidden;
    overflow-y: auto;
}

:deep(.modal-content) {
    position: relative;
    display: flex;
    flex-direction: column;
    max-height: 90%;
    margin: 0 1rem;
    padding: 1rem;
    border: 1px solid #e2e8f0;
    border-radius: 0.25rem;
    background: #fff;
}

.modal__title {
    margin: 0 2rem 0 0;
    font-size: 1.5rem;
    font-weight: 700;
}

.modal__content {
    flex-grow: 1;
    overflow-y: auto;
}

.modal__action {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    padding: 1rem 0 0;
}

.modal__close {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
}

@media (min-width: 768px) {
    .img-width {
        max-width: 126px !important;
    }
}

.brand-logo {
    width: 25px;
}

.tippy-box {
    overflow-wrap: break-word;
}

.dropdown-item {
    font-size: 14px !important;
    padding: 4px 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tabulator-header-filter input{
    border: 1px solid;
}

